@import "animate.css";
@import "common";
@import "./font/iconfont.css";

// @import "antd-mobile.css";
// @import "fonts/iconfont.css";


$base_color:#000;
$base_bgColor:#ccc;
$base_fontSize:28px;



html,body{
    overflow: scroll;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
}

html,body,#app,.main{
    width:100%;
    height:100%;
}


#app{
    color:$base_color;
    border:2px solid darkmagenta;
}

*{
    touch-action: none;
}
.box{
    color:red;
}

.txt{
    // border:2px solid darkmagenta;
}


.btn{
    border:1px solid darkcyan;
    border-radius: 5px;
    outline:none;
}

.enable{
    background: purple;
}
.disabled{
    background: darkgrey;
    cursor: not-allowed;
}

.moveIn{
    animation:zoomInDown .9s;
}

.moveOut{
    animation:zoomOutDown .9s;
}
.fadelns{
    animation:flipInX .9s;
}
